import React from 'react';
import NavBar from './pages/NavBar/NavBar';
import Home from './pages/Home/Home.js';
import ProjectsPage from './pages/Workshops/ProjectsPage.js';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from 'react-router-dom';
import AboutUs from './pages/AboutUs/AboutUs';
import Hackathon from './pages/Hackathon';
import Workshops from './pages/Workshops/Workshops';
import Schedule from './pages/Schedule/Schedule';
import Winners from './pages/Winners/Winners';
import OurTeam from './pages/OurTeam/OurTeam';
import IndustrialPartners from './pages/IndustrialPartners/IndustrialPartners';
import Footer from './pages/Footer/Footer';
import './App.css';

import '@fontsource/lexend-mega';
import '@fontsource/nunito-sans';

function App() {
  return <Router>
    <div id={'App'}>
      <Switch>
        <Route path = '/projects/:id' component = {ProjectsPage}>
        </Route>
        <Route path = '/'>
          <NavBar/>
          <Home/>
          <AboutUs/>
          <Hackathon/>
          <Workshops/>
          <Schedule/>
          <Winners/>
          <OurTeam/>
          <IndustrialPartners/>
          <Footer/>
        </Route>
      </Switch>
    </div>
  </Router>;
}

export default App;
