import React from 'react';
import { FaFacebook, FaInstagram } from 'react-icons/fa';
import { HiOutlineMail } from 'react-icons/hi';
import './Footer.css';

const Contact = (props) => {
    return (
        <a href={props.link} target={'_blank'} className={'contact'}>
            <props.icon/>
            <p className={'contactLink'}>{props.name}</p>
        </a>
    )
}

class Footer extends React.Component {
    render () {
        return (
            <div id={'contact'} className={'footer'}>
                <Contact icon={FaFacebook} link={'https://www.facebook.com/nitro.nlp'} name={'/nitro.nlp'}/>
                <Contact icon={FaInstagram} link={'https://www.instagram.com/nitro_nlp/'} name={'@nitro_nlp'}/>
                <Contact icon={HiOutlineMail} link={'mailto:hackatonnlp@gmail.com'} name={'hackatonnlp@gmail.com'}/>
            </div>
        );
    }
}

export default Footer;
